@import url(https://fonts.googleapis.com/css?family=Bilbo&effect=fire-animation);
@import url(https://fonts.googleapis.com/css?family=Festive);
@import url(https://fonts.googleapis.com/css?family=Yomogi);
@import url(https://fonts.googleapis.com/css?family=Tourney);
@import url(https://fonts.googleapis.com/css?family=Playfair Display);
@import url(https://fonts.googleapis.com/css?family=Lato);
@import url(https://fonts.googleapis.com/css?family=Oswald);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  margin: 0 auto;
  text-align: left;
}

.ulremovebullets {
  list-style-type: none;
}

.info {
  align-items: left;
  justify-content: left;
  font-size: calc(10px + 2vmin);
  color: rgb(189, 60, 60);
  text-align: center;
  font-family: "Playfair Display";

}

.container {
  background-color: #eb991e;
  min-height: 100vh;


  font-size: calc(10px + 2vmin);
  color: rgb(224, 224, 224);
  font-family: "Lato";

}

.result-info {
  font-size: .5em;
  font-weight: bold;
  margin-bottom: 5px;
  color: rgba(57, 16, 78, 0.925);
  font-family: "Oswald", sans-serif;

}







.title {
  position: relative;
  background-color: rgb(189, 60, 60);
  color: rgb(255, 255, 255);
  padding: 20px;
  font-size: 1.8em;
  text-align: center;

  font-family: "Bilbo";

}

.question {
  font-size: 1.4em;
  font-weight: bold;
  margin-bottom: 5px;
  color: rgba(57, 16, 78, 0.925);
  font-family: "Oswald", sans-serif;

}

.questionBox {


  border: 1px solid rgba(226, 56, 56, 0.699);
  margin-top: 15px;
  padding: 15px;
  box-sizing: border-box;
  background-color: rgba(189, 60, 60);
  border-radius: 10px;
  margin-right: 40px
}

.answers {
  display: inline-block;



}

.answerBtn {
  position: relative;
  padding: 10px;
  background-color: rgb(0, 0, 0);
  display: inline-block;
  margin: 10px;
  outline: none;
  border: none;
  font-size: 1em;
  color: rgb(67, 24, 223);
  font-weight: bold;
  border-radius: 4px;
  cursor: pointer;
}

.wrapper-class input[type="radio"] {
  width: 15px;
}

.wrapper-class label {
  display: inline;
  margin-left: 5px;
}


